import React from 'react';
import s from './style.module.scss'
import {cards} from "./data";
import Image from 'react-bootstrap/Image'
import {Carousel} from "react-bootstrap";
import {useSelector} from "react-redux";

const Home = () => {
    const slides = cards.map((el) => {
        return (
            <Carousel.Item className={s.carousel_item} key={el.id}>
                <div style={{backgroundImage: `url(${el.img})`}} className={s.img}/>
            </Carousel.Item>
        )
    })

    return (
        <div className={s.home}>

            <div className={s.info_container}>
                <h1>     <span className={s.name}>3-.</span></h1>
                <Carousel className={s.carousel}>
                    {slides}
                </Carousel>
                <p className={s.before1}>    3D   
                    .   
                       ,   ,-,  
                    
                         .</p>
                <p className={s.before2}>  -   ,  
                       3D
                    .         , , 
                     ,      .</p>

                <p className={s.before3}>  <span className={s.name}>3-.</span> 
                       
                       
                      
                       .</p>


                <h2>  :</h2>

                <ul className={s.ul}>
                    <li>      (-)</li>
                    <li>      </li>
                    <li>    </li>
                    <li>      </li>
                    <li>        3-15 </li>
                    <li>    </li>
                    <li>      </li>
                </ul>
            </div>


        </div>
    );
};

export default Home;
